import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Fab from "@mui/material/Fab";

const dispatchedEvents = [];

const SrsMuiFab = function (props, ref) {
  return (
    <Fab {...props} theme={props.muiTheme}>
      <slot />
    </Fab>
  );
};

const styles = `
  /* Custom styles for the MuiFab component */
`;

const props = [
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableFocusRipple",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableRipple",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiFab if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiFab), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
